*{padding:0;margin:0;font-family:"roboto"}:root{--background: #141413;--foreground: #f8faf0}@media only screen and (max-width: 600px){:root{--background: #141413;--foreground: #f8faf0}}header{width:75vw;display:flex;flex-direction:row;gap:12px;justify-content:flex-end;margin:35px 0 0 0;font-size:10px}body{color:var(--foreground);background:var(--background);font-family:Arial,Helvetica,sans-serif;background-position:top;background-repeat:no-repeat;background-size:cover;height:100%}@media only screen and (max-width: 600px){body{background-image:none}}main{display:flex;justify-content:center}a{text-decoration:none;color:var(--foreground)}.header-container{width:100vw;display:flex;align-items:center;justify-content:center}